<template>
  <div class="p-3">

    <div class="mb-3 p-3 small">
      <div class="input-group">
        <input type="search" class="form-control" placeholder="جستجو میان ویدئوها"/>
        <div class="input-group-append">
          <input type="button" class="btn btn-primary" value="جستجو"/>
        </div>
      </div>
      <div class="mt-1">
        <b class="font-weight-bolder m-1">مرتب سازی بر اساس:</b>

        <button class="btn btn-sm m-1" :class="sortType === 'alphabet-asc' ? 'btn-info' : 'btn-light'"
                @click="sortType = 'alphabet-asc'">
          <span class="fa fa-sort-alpha-up mr-1"></span>
          <span>نام صعودی</span>
        </button>

        <button class="btn btn-sm m-1" :class="sortType === 'alphabet-desc' ? 'btn-info' : 'btn-light'"
                @click="sortType = 'alphabet-desc'">
          <span class="fa fa-sort-alpha-down mr-1"></span>
          <span>نام نزولی</span>
        </button>

        <button class="btn btn-sm m-1" :class="sortType === 'last-published' ? 'btn-info' : 'btn-light'"
                @click="sortType = 'last-published'">
          <span class="fa fa-sort-numeric-up mr-1"></span>
          <span>تاریخ انتشار</span>
        </button>
      </div>
    </div>

    <div class="table-responsive">
      <table class="table table-bordered table-striped">
        <thead class="thead-dark text-center">
        <th>عنوان</th>
        <th>نوع</th>
        <th>قیمت</th>
        <th>تخفیف</th>
        <th>وضعیت</th>
        </thead>

        <tbody>

        <tr v-for="(x,i) in items" :key="i" class="text-center">
          <td class="text-left">{{ x.name }}</td>
          <td>{{ x.type }}</td>
          <td>{{ x.price }}</td>
          <td>{{ x.off }}</td>
          <td :class="x.status | statusCssClass">{{ x.status |statusValue }}</td>
        </tr>

        </tbody>
      </table>
    </div>

    <div class="my-3">
      <nav aria-label="Page navigation example">
        <ul class="pagination justify-content-center">
          <li class="page-item"><a class="page-link" href="#">قبلی</a></li>
          <li class="page-item active"><a class="page-link" href="#">1</a></li>
          <li class="page-item"><a class="page-link" href="#">2</a></li>
          <li class="page-item"><a class="page-link" href="#">3</a></li>
          <li class="page-item"><a class="page-link" href="#">بعدی</a></li>
        </ul>
      </nav>
    </div>

  </div>
</template>

<script>
export default {
  name: "StoreItems",
  data: () => ({
    items: [],
    sortType: 'alphabet-asc'
  }),
  mounted: function () {
    this.items = [
      {name: 'نام شماره یک', type: 'کالای فیزیکی', price: '3,500', off: '100', status: 255},
      {name: 'نام شماره دو', type: 'خدمات دیجیتال', price: '1,200', off: '200', status: 255},
      {name: 'نام شماره سه', type: 'فایل برای دانلود', price: '5,520', off: '300', status: 1},
      {name: 'نام شماره چهار', type: 'کالای فیزیکی', price: '8,100', off: '0', status: 0},
      {name: 'نام شماره پنج', type: 'خدمات در محل', price: '500', off: '10', status: 0},
      {name: 'نام شماره شش', type: 'کالای فیزیکی', price: '52,900', off: '50', status: 255},
      {name: 'نام شماره هفت', type: 'فایل برای دانلود', price: '1,200', off: '0', status: 1},
      {name: 'نام شماره هشت', type: 'خدمات دیجیتال', price: '5,500', off: '0', status: 0},
      {name: 'نام شماره نه', type: 'فایل برای دانلود', price: '8,400', off: '10', status: 255},
      {name: 'نام شماره ده', type: 'کالای فیزیکی', price: '13,100', off: '90', status: 255},
      {name: 'نام شماره یازده', type: 'خدمات در محل', price: '23,900', off: '800', status: 255},
    ]
  },
  filters: {
    statusValue: function (x) {
      switch (x) {
        case 255:
          return 'تایید شده';
        case 0:
          return 'درحال بررسی';
        case 1:
          return 'عدم تایید'
        default:
          return '';
      }
    },
    statusCssClass: function (x) {
      switch (x) {
        case 255:
          return 'text-success';
        case 0:
          return 'text-info';
        case 1:
          return 'text-danger'
        default:
          return 'text-body';
      }
    }
  }
}
</script>

